import React, { useState } from 'react'

export default function TextForm(props) {

    const [text, setText] = useState("")
    const [text2, setText2] = useState("")

    const handleUpClick = () =>{
        let newText = text.toUpperCase();
        let newText2 = text2.toUpperCase();
        setText(newText)
        setText2(newText2)
        props.showAlert("Convert to Uppercase", "info")
    }

    const handleLoClick = () =>{
        let newText = text.toLowerCase();
        let newText2 = text2.toLowerCase();
        setText(newText)
        setText2(newText2)
        props.showAlert("Convert to Lowercase", "info")
    }

    const handleCpClick = (word) =>{
        let newText = text.replace(/\b[a-z]/g, match => match.toUpperCase());
        let newText2 = text2.replace(/\b[a-z]/g, match => match.toUpperCase());
        setText(newText)
        setText2(newText2)
    }

    const handleClearClick = () =>{
        let newText = '';
        let newText2 = '';
        setText(newText)
        setText2(newText2)
    }

    const onChangeInput =(event)=>{
        setText(event.target.value)
    }
    const onChangeTextarea =(event)=>{
        setText2(event.target.value)
    }
    

    return (
        <div>
            <div className="container my-4">
                <h2>{props.heading}</h2>
                <p>Name : {text.length>0?text:"No Name"}</p>
                <p>Description : {text2.length>0?text2:"No Discription"}</p>
                <input type="text" className="form-control" placeholder="Enter Name" value={text} onChange={onChangeInput} />
                <textarea className="form-control my-3" placeholder="Enter Description" value={text2} onChange={onChangeTextarea}></textarea>
                <button className="btn btn-lg btn-primary" onClick={handleUpClick} >Uppercase</button>
                <button className="btn btn-lg btn-primary mx-2" onClick={handleLoClick} >Lowercase</button>
                <button className="btn btn-lg btn-primary mx-2" onClick={handleCpClick}>Capitalized</button>
                <button className="btn btn-lg btn-danger mr-2" onClick={handleClearClick}>Clear</button>
                <hr />
                <table className="table table-bordered">
                    <thead>
                        <tr>
                            <th>Words</th>
                            <th>Charactors</th>
                            <th>Slow (125 wpm)</th>
                            <th>Average (300 wpm)</th>
                            <th>Fast (450 wpm)</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{text2.split(" ").length-1}</td>
                            <td>{text2.length}</td>
                            <td>{0.008 * text2.split(" ").length}</td>
                            <td>{0.0032 * text2.split(" ").length}</td>
                            <td>{0.0024 * text2.split(" ").length}</td>
                        </tr>
                    </tbody>
                </table>
               
            </div>
        </div>
    )
}
