import React from 'react'

export default function Alert(props) {
    return (
        props.alert &&  <div className="container my-3 alert alert-info alert-dismissible fade show" role="alert">
            <strong>{props.alert.no}, {props.alert.msg}, {props.alert.type}</strong>
            <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
        </div>
    )
}
