import React, {useState} from 'react'

export default function About() {

    const [myStyle, setMyStyle] = useState({
        color: 'black',
        background: 'white'
    })

    const [modeText, setModeText] = useState('Light')

    const toggleStyle = () =>{
        if (myStyle.color === 'white') {
            setMyStyle({
                color: 'black',
                background: 'white'
            })
            setModeText('Light')
            

        } else {
            setMyStyle({
                color: 'white',
                background: 'black'
            })
            setModeText('Dark')
        }
    }

    return (
        <div className="container-fluid" style={myStyle}>
            <div className="container py-5">
                <h1>{modeText} mode hero</h1>
                <button onClick={toggleStyle} className="btn btn-primary my-3">test</button>
                <p>Quickly design and customize responsive mobile-first sites with Bootstrap, the worldâ€™s most popular front-end open source toolkit, featuring Sass variables and mixins, responsive grid system, extensive prebuilt components, and powerful JavaScript plugins.</p>
                <img alt="Test" className="img-fluid" src="https://getbootstrap.com/docs/5.1/examples/heroes/bootstrap-docs.png" />
            </div>
        </div>
    )
}
