import React from 'react'
import PropTypes from 'prop-types'
import logo from './../logo.svg';
// import { Link } from 'react-router-dom';


export default function Navbar(props) {

    const toggleColorWhite = () =>{
        document.body.className = 'white'
    }
    const toggleColorBlack = () =>{
        document.body.className = 'black'
    }
    const toggleColorRed = () =>{
        document.body.className = 'red'
    }
    const toggleColorBlue = () =>{
        document.body.className = 'blue'
    }

    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
            <div className="container">
                <a className="navbar-brand" href="#"><img src={logo} className="App-logo" alt="logo" /></a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav me-auto">
                        <a className="nav-link" href="#">{props.title}</a>
                        <a className="nav-link active" aria-current="page" href="#">Home</a>
                        <a className="nav-link" href="#">About</a>
                    </div>
                    <div>
                        <ul className="color-palette">
                            <li className="white" onClick={toggleColorWhite}></li>
                            <li className="black" onClick={toggleColorBlack}></li>
                            <li className="red" onClick={toggleColorRed}></li>
                            <li className="blue" onClick={toggleColorBlue}></li>
                        </ul>
                    </div>
                    <div className={`form-check form-switch text-${props.mode === 'light' ? 'dark' : 'light'}`}>
                        <input className="form-check-input" type="checkbox" role="switch" onClick={props.toggleSwitch} id="flexSwitchCheckDefault" />
                        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Default switch checkbox input</label>
                    </div>
                </div>
            </div>
        </nav>
    )
}



Navbar.propTypes = {
    title: PropTypes.string.isRequired
}

Navbar.defaultProps = {
    title: 'Navbar Title'
}